.login-register-page .title {
  margin-top: 10px;
  font-size: 1.8rem; }

.login-register-page .paper {
  height: auto;
  width: 400px;
  padding-top: 10px;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 30px;
  text-align: center;
  margin: 20px 10px; }

.login-register-page .facebook-google-icon {
  margin-left: 2px;
  margin-right: 2px; }

.login-register-page .forgot-password {
  color: #03A9F4;
  float: left;
  text-decoration: none;
  cursor: pointer; }

.login-register-page .create-account {
  color: #03A9F4;
  margin-left: 10px;
  text-decoration: none; }

.active {
  background-color: white;
  color: black; }

.topic, .exam {
  margin: 25px;
  height: 100px;
  padding: 10px;
  position: relative; }
  .topic a, .exam a {
    position: absolute;
    bottom: 10px;
    right: 10px; }

.topic img {
  height: 100%; }

.topic .name {
  margin-left: 29px;
  font-size: 1.2rem; }

.exam .name {
  font-weight: 700;
  margin-right: 30px; }

.exam .detail {
  padding: 10px 15px; }

.question {
  margin: 15px;
  padding: 10px; }
  .question .name {
    font-weight: 700;
    margin-right: 25px; }
  .question ul {
    margin-right: 10px; }

@media all and (max-width: 480px) {
  .login-register-page .title {
    font-size: 1.5rem; }
  .login-register-page .paper {
    width: 360px; } }

@media all and (max-width: 320px) {
  .login-register-page .title {
    font-size: 1.3rem; }
  .login-register-page .paper {
    width: 300px;
    padding: 10px 30px 20px 30px; } }



